/** @format */

.blog__meta {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.Blog__meta.absolute {
  top: 0%;
  padding: 0px 20px;
  left: 0%;
  display: flex;
  height: 100%;
  align-items: flex-start;
  justify-content: flex-end;
  flex-direction: column;
  flex-wrap: wrap;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}
a.blog.project__btn {
  margin-bottom: -50px;
  transition: 0.4s all ease;
}
.Blog__meta:hover a.blog.project__btn {
  margin-bottom: 40px;
}
